  border: 0;
  border-radius: 2px;
  color: #ffffff;
  cursor: pointer;
  overflow: hidden;
  padding: 10px 15px;
  position: relative;
  text-decoration: none;
  width: auto;
}

.btn:hover, .btn:focus {
